<template><div class="markdown-body"><h1>进度条</h1>
<p><code v-pre="">mu-linear-progress</code> 和 <code v-pre="">mu-circular-progress</code> 用于向用户显示数据加载进度。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex class=\&quot;demo-linear-progress\&quot;&gt;\n    &lt;mu-linear-progress&gt;&lt;/mu-linear-progress&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-flex class=\&quot;demo-linear-progress\&quot;&gt;\n    &lt;mu-linear-progress color=\&quot;secondary\&quot;&gt;&lt;/mu-linear-progress&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-flex class=\&quot;demo-linear-progress\&quot;&gt;\n    &lt;mu-linear-progress :value=\&quot;linear\&quot; mode=\&quot;determinate\&quot; color=\&quot;green\&quot;&gt;&lt;/mu-linear-progress&gt;\n  &lt;/mu-flex&gt;\n&lt;/mu-container&gt;\n\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      linear: 10\n    }\n  },\n  created () {\n    this.timer = setInterval(() =&gt; {\n      this.linear += 10;\n      if (this.linear &gt; 100) this.linear = 0;\n    }, 1000)\n  },\n  beforeDestroy () {\n    if (this.timer) clearInterval(this.timer);\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.demo-linear-progress {\n  margin: 16px 0;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex class="demo-linear-progress">
    <mu-linear-progress></mu-linear-progress>
  </mu-flex>
  <mu-flex class="demo-linear-progress">
    <mu-linear-progress color="secondary"></mu-linear-progress>
  </mu-flex>
  <mu-flex class="demo-linear-progress">
    <mu-linear-progress :value="linear" mode="determinate" color="green"></mu-linear-progress>
  </mu-flex>
</mu-container>



</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-linear-progress"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-linear-progress</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-linear-progress</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-linear-progress"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-linear-progress</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"secondary"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-linear-progress</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-linear-progress"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-linear-progress</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"linear"</span> <span class="hljs-attr">mode</span>=<span class="hljs-string">"determinate"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"green"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-linear-progress</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">linear</span>: <span class="hljs-number">10</span>
    }
  },
  created () {
    <span class="hljs-keyword">this</span>.timer = setInterval(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
      <span class="hljs-keyword">this</span>.linear += <span class="hljs-number">10</span>;
      <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.linear &gt; <span class="hljs-number">100</span>) <span class="hljs-keyword">this</span>.linear = <span class="hljs-number">0</span>;
    }, <span class="hljs-number">1000</span>)
  },
  beforeDestroy () {
    <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.timer) clearInterval(<span class="hljs-keyword">this</span>.timer);
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.demo-linear-progress</span> {
  <span class="hljs-attribute">margin</span>: <span class="hljs-number">16px</span> <span class="hljs-number">0</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="bu-tong-de-se-cai-he-da-xiao"><a class="header-anchor" href="#bu-tong-de-se-cai-he-da-xiao" aria-hidden="true">¶</a> 不同的色彩和大小</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex class=\&quot;demo-linear-progress\&quot;&gt;\n    &lt;mu-linear-progress mode=\&quot;determinate\&quot; :value=\&quot;30\&quot;&gt;&lt;/mu-linear-progress&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-flex class=\&quot;demo-linear-progress\&quot;&gt;\n    &lt;mu-linear-progress mode=\&quot;determinate\&quot; :value=\&quot;60\&quot; :size=\&quot;10\&quot; color=\&quot;secondary\&quot;&gt;&lt;/mu-linear-progress&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-flex class=\&quot;demo-linear-progress\&quot;&gt;\n    &lt;mu-linear-progress mode=\&quot;determinate\&quot; :value=\&quot;90\&quot; :size=\&quot;15\&quot; color=\&quot;green\&quot;&gt;&lt;/mu-linear-progress&gt;\n  &lt;/mu-flex&gt;\n&lt;/mu-container&gt;\n\n&quot;,&quot;script&quot;:null,&quot;style&quot;:&quot;\n.demo-linear-progress {\n  margin: 16px 0;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex class="demo-linear-progress">
    <mu-linear-progress mode="determinate" :value="30"></mu-linear-progress>
  </mu-flex>
  <mu-flex class="demo-linear-progress">
    <mu-linear-progress mode="determinate" :value="60" :size="10" color="secondary"></mu-linear-progress>
  </mu-flex>
  <mu-flex class="demo-linear-progress">
    <mu-linear-progress mode="determinate" :value="90" :size="15" color="green"></mu-linear-progress>
  </mu-flex>
</mu-container>

</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-linear-progress"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-linear-progress</span> <span class="hljs-attr">mode</span>=<span class="hljs-string">"determinate"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"30"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-linear-progress</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-linear-progress"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-linear-progress</span> <span class="hljs-attr">mode</span>=<span class="hljs-string">"determinate"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"60"</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"10"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"secondary"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-linear-progress</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-linear-progress"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-linear-progress</span> <span class="hljs-attr">mode</span>=<span class="hljs-string">"determinate"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"90"</span>  <span class="hljs-attr">:size</span>=<span class="hljs-string">"15"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"green"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-linear-progress</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.demo-linear-progress</span> {
  <span class="hljs-attribute">margin</span>: <span class="hljs-number">16px</span> <span class="hljs-number">0</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="huan-xing-jin-du-tiao"><a class="header-anchor" href="#huan-xing-jin-du-tiao" aria-hidden="true">¶</a> 环形进度条</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-circular-progress class=\&quot;demo-circular-progress\&quot; :size=\&quot;36\&quot;&gt;&lt;/mu-circular-progress&gt;\n&lt;mu-circular-progress class=\&quot;demo-circular-progress\&quot; color=\&quot;secondary\&quot; :stroke-width=\&quot;5\&quot; :size=\&quot;48\&quot;&gt;&lt;/mu-circular-progress&gt;\n&lt;mu-circular-progress class=\&quot;demo-circular-progress\&quot; color=\&quot;warning\&quot; :stroke-width=\&quot;7\&quot; :size=\&quot;56\&quot;&gt;&lt;/mu-circular-progress&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-circular-progress class="demo-circular-progress" :size="36"></mu-circular-progress>
<mu-circular-progress class="demo-circular-progress" color="secondary" :stroke-width="5" :size="48"></mu-circular-progress>
<mu-circular-progress class="demo-circular-progress" color="warning" :stroke-width="7" :size="56"></mu-circular-progress>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-circular-progress</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-circular-progress"</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"36"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-circular-progress</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">mu-circular-progress</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-circular-progress"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"secondary"</span> <span class="hljs-attr">:stroke-width</span>=<span class="hljs-string">"5"</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"48"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-circular-progress</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">mu-circular-progress</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-circular-progress"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">:stroke-width</span>=<span class="hljs-string">"7"</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"56"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-circular-progress</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="ding-zhi-yuan-huan"><a class="header-anchor" href="#ding-zhi-yuan-huan" aria-hidden="true">¶</a> 定值圆环</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-circular-progress class=\&quot;demo-circular-progress\&quot; mode=\&quot;determinate\&quot; :value=\&quot;40\&quot; :size=\&quot;56\&quot;&gt;&lt;/mu-circular-progress&gt;\n&lt;mu-circular-progress class=\&quot;demo-circular-progress\&quot; mode=\&quot;determinate\&quot; :value=\&quot;60\&quot; color=\&quot;secondary\&quot; :stroke-width=\&quot;5\&quot; :size=\&quot;72\&quot;&gt;&lt;/mu-circular-progress&gt;\n&lt;mu-circular-progress class=\&quot;demo-circular-progress\&quot; mode=\&quot;determinate\&quot; :value=\&quot;80\&quot; color=\&quot;warning\&quot; :stroke-width=\&quot;7\&quot; :size=\&quot;96\&quot;&gt;&lt;/mu-circular-progress&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-circular-progress class="demo-circular-progress" mode="determinate" :value="40" :size="56"></mu-circular-progress>
<mu-circular-progress class="demo-circular-progress" mode="determinate" :value="60" color="secondary" :stroke-width="5" :size="72"></mu-circular-progress>
<mu-circular-progress class="demo-circular-progress" mode="determinate" :value="80" color="warning" :stroke-width="7" :size="96"></mu-circular-progress>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-circular-progress</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-circular-progress"</span> <span class="hljs-attr">mode</span>=<span class="hljs-string">"determinate"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"40"</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"56"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-circular-progress</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">mu-circular-progress</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-circular-progress"</span> <span class="hljs-attr">mode</span>=<span class="hljs-string">"determinate"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"60"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"secondary"</span> <span class="hljs-attr">:stroke-width</span>=<span class="hljs-string">"5"</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"72"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-circular-progress</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">mu-circular-progress</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-circular-progress"</span> <span class="hljs-attr">mode</span>=<span class="hljs-string">"determinate"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"80"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">:stroke-width</span>=<span class="hljs-string">"7"</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"96"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-circular-progress</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="linear-progress-props"><a class="header-anchor" href="#linear-progress-props" aria-hidden="true">¶</a> Linear Progress Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>mode</td>
<td>显示模式， indeterminate 不确定进度模式，这时 value、min、max 无效, determinate 需要通过改变value值来改变进度显示</td>
<td>String</td>
<td>indeterminate/determinate</td>
<td>indeterminate</td>
</tr>
<tr>
<td>max</td>
<td>最大值</td>
<td>Number</td>
<td>—</td>
<td>100</td>
</tr>
<tr>
<td>min</td>
<td>最小值</td>
<td>Number</td>
<td>—</td>
<td>0</td>
</tr>
<tr>
<td>value</td>
<td>进度显示值</td>
<td>Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>size</td>
<td>进度条高度</td>
<td>Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>color</td>
<td>颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="circular-progress-props"><a class="header-anchor" href="#circular-progress-props" aria-hidden="true">¶</a> Circular Progress Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>mode</td>
<td>显示模式， indeterminate 不确定进度模式，这时 value、min、max 无效, determinate 需要通过改变value值来改变进度显示</td>
<td>String</td>
<td>indeterminate/determinate</td>
<td>indeterminate</td>
</tr>
<tr>
<td>max</td>
<td>最大值</td>
<td>Number</td>
<td>—</td>
<td>100</td>
</tr>
<tr>
<td>min</td>
<td>最小值</td>
<td>Number</td>
<td>—</td>
<td>0</td>
</tr>
<tr>
<td>value</td>
<td>进度显示值</td>
<td>Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>size</td>
<td>圆环的尺寸</td>
<td>Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>stroke-width</td>
<td>圆环的宽度</td>
<td>Number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>color</td>
<td>颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>


</div class="markdown-body"></template>
<style>
.demo-linear-progress {
  margin: 16px 0;
}
.demo-circular-progress {
  margin: 0 16px;
}
</style>
<script>
export default {
  data () {
    return {
      linear: 10
    }
  },
  created () {
    this.timer = setInterval(() => {
      this.linear += 10;
      if (this.linear > 100) this.linear = 0;
    }, 1000)
  },
  beforeDestroy () {
    if (this.timer) clearInterval(this.timer);
  }
}
</script>


